footer {
  background: linear-gradient(to right, #003666 90%, #5fac28 10%);
  height: 327px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  @include media(mobile) {
    height: 160px;
  }

  img {
    @include media(mobile) {
      transform: scale(0.5);
    }
  }
}
