#how-work {
  @include media(mobile) {
    margin-bottom: 50px;
  }
  .shell {
    padding: 0 126px;

    @include media(mobile) {
      padding: 0 32px;
    }
  }
  h1 {
    @include fontMontserrat(100px, 700);
    color: $white;
    line-height: 138px;
    text-transform: uppercase;

    @include media(mobile) {
      @include fontMontserrat(32px, 700);
      color: $white;
      line-height: 50px;
      margin-top: 38px;
    }

    &:before {
      content: "";
      background: $blue;
      width: 1437px;
      height: 130px;
      display: block;
      position: absolute;
      z-index: -1;
      margin: 0 -305px;

      @include media(mobile) {
        height: 61px;
        margin-top: -7px;
      }
    }

    span {
      @include fontMontserrat(74px, 700);
      color: $aqua;
      text-transform: uppercase;

      @include media(mobile) {
        @include fontMontserrat(25px, 700);
        color: $aqua;
        margin: -100px 0;
      }
    }
  }

  .boxes-content {
    display: flex;
    justify-content: space-between;

    @include media(mobile) {
      flex-direction: column;
    }

    .content-left {
      width: 44%;

      @include media(mobile) {
        width: 100%;
        text-align: center;
      }

      h2 {
        @include fontMontserrat(30px, 700);
        color: $black;
        margin-bottom: 52px;

        @include media(mobile) {
          @include fontMontserrat(24px, 700);
          padding: 0 40px;
          margin-bottom: 0;

          br {
            display: none;
          }
        }
      }

      .homeair-mb {
        @include media(desktop) {
          display: none;
        }
      }

      p {
        @include fontMontserrat(22px, 500);
        color: $black;
        line-height: 30px;

        &:first-of-type {
          margin-bottom: 68px;

          @include media(mobile) {
            margin: 0;
            padding: 0 40px;
          }
        }

        &:last-of-type {
          margin-bottom: 45px;
        }

        @include media(mobile) {
          line-height: 20px;
          font-size: 14px;

          br {
            display: none;
          }
        }
      }

      .boxes {
        display: flex;
      }

      .sm-box {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        background: rgba($light-blue, 0.1);
        width: 234px;
        height: 241px;
        padding: 14px;
        border-radius: 12px;
        margin-right: 12.5px;

        @include media(mobile) {
          width: 50%;
          height: 170px;
        }

        img {
          margin-bottom: 32px;
        }

        .fan {
          width: 100px;
          height: 80px;
        }

        .home {
          width: 185px;
          height: 80px;
        }

        p {
          @include fontMontserrat(22px, 500);
          margin: 0;

          @include media(mobile) {
            @include fontMontserrat(18px, 500);
          }

          &:last-of-type {
            @include fontMontserrat(17px, 500);

            @include media(mobile) {
              @include fontMontserrat(13px, 500);
            }
          }
        }
      }
    }
    .content-right {
      img {
        position: relative;
        right: 0;
        z-index: -1;
        bottom: 114px;
        left: 162px;
        transform: scale(1.4);

        @include media(mobile) {
          &:nth-child(1) {
            display: none;
          }
        }
      }
    }
  }
}

#air-parts {
  .shell {
    padding: 0 54px;
  }
  .boxes-content {
    display: flex;

    @include media(mobile) {
      flex-direction: column;
    }

    h2 {
      @include fontMontserrat(55px, 700);
      line-height: 64px;
      color: $lightest-blue;
      text-transform: uppercase;
      margin-bottom: 56px;

      @include media(mobile) {
        @include fontMontserrat(32px, 700);
        margin: 0;
        line-height: 30px;
        color: $lightest-blue;
        margin-bottom: 20px;
      }

      span {
        color: $cold;

        @include media(mobile) {
          @include fontMontserrat(25px, 700);
          color: $cold;
        }
      }
    }
    p {
      @include fontMontserrat(22px, 500);
      margin-bottom: 47px;

      @include media(mobile) {
        @include fontMontserrat(18px, 500);

        br {
          display: none;
        }
      }
    }
  }
  .content-left {
    width: 56%;
    @include media(mobile) {
      width: 100%;
    }

    .box-air {
      width: 587px;
      height: 1005px;
      padding: 60px;
      background: rgba($light-blue, 0.05);
      border-radius: 32px;
      margin: 0 -60px;

      @include media(mobile) {
        width: 100%;
        height: 650px;
        border-radius: 0;
        margin: 0;
        padding: 20px;
      }

      p {
        @include fontMontserrat(22px, 500);
        line-height: 33px;

        @include media(mobile) {
          @include fontMontserrat(18px, 500);
        }

        span {
          @include fontMontserrat(40px, 700);
          color: $gray;

          @include media(mobile) {
            @include fontMontserrat(22px, 700);
            color: $gray;
          }
        }
      }

      .title {
        @include fontMontserrat(40px, 700);
        margin-bottom: 7.5px;

        @include media(mobile) {
          @include fontMontserrat(24px, 700);
        }
      }

      .bg {
        position: absolute;
        margin: -550px -67px;
        pointer-events: none;
        transform: scale(0.99);

        @include media(mobile) {
          display: none;
        }
      }

      .bg-mb {
        position: absolute;
        margin: -150px -90px;
        transform: scale(0.8);

        @include media(desktop) {
          display: none;
        }
      }
    }
  }
  .content-right {
    padding-top: 100px;

    @include media(mobile) {
      padding: 14px;
      margin-top: 65px;
      background: rgba($light-blue, 0.05);
      border-radius: 32px;
      margin-bottom: 40px;
    }

    p {
      @include fontMontserrat(22px, 500);
      line-height: 30px;
      span {
        @include fontMontserrat(40px, 700);
        color: $gray;
      }

      @include media(mobile) {
        @include fontMontserrat(14px, 500);
        line-height: 27px;
        text-align: right;
      }
    }

    .title {
      @include fontMontserrat(40px, 700);
      margin-bottom: 7.5px;

      @include media(mobile) {
        @include fontMontserrat(20px, 700);
        margin: -46px 0;
      }
    }

    .box-condition {
      width: 587px;
      height: 804px;
      padding: 560px 60px 60px;
      background: rgba($light-blue, 0.05);
      border-radius: 32px;
      margin: 0 -60px 12.7px;

      @include media(mobile) {
        background: none;
        width: 100%;
        height: auto;
        padding: 220px 20px 0px;
        margin: 0;
      }

      p {
        @include media(mobile) {
          text-align: left;
          line-height: 20px;
          margin-bottom: 10px;
          br {
            display: none;
          }

          span {
            font-size: 20px;
          }
        }
      }
    }

    .box-ray {
      padding: 60px;
      width: 587px;
      background: rgba($light-blue, 0.2);
      border-radius: 32px;
      margin: 0 -60px;

      @include media(mobile) {
        width: 100%;
        margin: 0;
        display: none;
      }

      p {
        @include fontMontserrat(27px, 500);
        color: $light-blue;
        line-height: 35px;
      }

      span {
        @include fontMontserrat(40px, 700);
        color: $light-blue;
      }
    }
  }

  .box-ray-mb {
    padding: 30px;
    width: 100%;
    background: rgba($light-blue, 0.2);
    height: 200px;
    display: flex;
    align-items: center;

    @include media(desktop) {
      display: none;
    }
    .content-box {
      display: flex;
      justify-content: space-between;
    }
    .box-item {
      &:first-of-type {
        width: 75%;
      }
    }

    p {
      @include fontMontserrat(14px, 400);
      color: $light-blue;
      line-height: 20px;
    }

    span {
      @include fontMontserrat(24px, 700);
      color: $light-blue;
    }
  }
}

#compressor {
  margin-bottom: 100px;

  @include media(mobile) {
    margin: 50px 0;
  }

  .list {
    margin: 30px 0;
    padding: 0 50px;
    li {
      margin-bottom: 10px;
      p {
        @include fontMontserrat(16px, 500);
        color: $light-blue;

        strong {
          @include fontMontserrat(18px, 700);
          color: $light-blue;
        }
      }
    }

    @include media(desktop) {
      display: none;
    }
  }
  .title {
    @include fontMontserrat(40px, 700);
    text-transform: uppercase;
    padding: 0 54px;

    @include media(mobile) {
      @include fontMontserrat(20px, 700);
      text-align: center;
      padding: 0;
      margin-bottom: 50px;
    }
  }

  .compressor-desk {
    margin: -140px 0;
    @include media(mobile) {
      display: none;
    }
  }

  .compressor-mb {
    margin: 0;

    @include media(desktop) {
      display: none;
    }
  }
}

#account {
  .shell {
  }
  .account-data {
    border-radius: 16px;
    padding: 90px 131px;
    background: rgba($blue, 0.05);
    margin-bottom: 85px;

    @include media(mobile) {
      padding: 36px 25px;
      margin-bottom: 42px;
    }

    .content-account {
      display: flex;
      justify-content: space-between;
      margin-bottom: 70px;

      @include media(mobile) {
        flex-direction: column;
      }

      h3 {
        @include fontMontserrat(44px, 700);
        line-height: 52px;
        margin-bottom: 96px;

        @include media(mobile) {
          @include fontMontserrat(28px, 700);
          line-height: 30px;
          margin-bottom: 32px;
        }
      }

      .box-left-economy {
        .box {
          display: flex;

          @include media(mobile) {
            flex-direction: column;
          }

          div {
            width: 50%;

            @include media(mobile) {
              width: 76%;
            }
          }
          p {
            @include fontMontserrat(22px, 500);
            color: $black;

            @include media(mobile) {
              @include fontMontserrat(16px, 500);
            }

            span {
              @include fontMontserrat(44px, 700);
              line-height: 52.8px;
              color: $blue-xl;

              @include media(mobile) {
                @include fontMontserrat(32px, 700);
                color: $blue-xl;
              }

              strong {
                @include fontMontserrat(144px, 700);
                color: $blue-xl;

                span {
                  @include fontMontserrat(54px, 700);
                  color: $blue-xl;

                  @include media(mobile) {
                    @include fontMontserrat(40px, 700);
                    color: $blue-xl;
                  }
                }
              }
            }
          }

          .hour {
            p {
              @include fontMontserrat(22px, 500);
              color: $black;

              @include media(mobile) {
                @include fontMontserrat(18px, 500);
              }

              span {
                @include fontMontserrat(44px, 700);
                line-height: 52.8px;
                color: $blue-xl;
                position: relative;
                top: 50px;

                strong {
                  @include fontMontserrat(144px, 700);
                  color: $blue-xl;

                  span {
                    @include fontMontserrat(54px, 700);
                    color: $blue-xl;
                    position: relative;
                    top: -6px;

                    @include media(mobile) {
                      @include fontMontserrat(32px, 700);
                      color: $blue-xl;
                    }
                  }
                }
              }
            }
          }
        }
      }

      .box-right-economy {
        img {
          position: relative;
          left: 164px;
          bottom: 70px;

          @include media(mobile) {
            position: absolute;
            left: auto;
            bottom: auto;
            margin: -250px 55px;
            transform: scale(0.50);
          }
        }
      }
    }

    .content-money {
      display: flex;
      justify-content: center;
      margin-bottom: 70px;

      @include media(mobile) {
        flex-direction: column;
      }

      .box-money {
        @include media(mobile) {
          img {
            margin: 0 auto;
          }
        }
      }
      .title {
        text-transform: uppercase;
        @include fontMontserrat(22px, 700);
        color: $black;
        position: absolute;
        margin: 105px 0;
        line-height: 26px;

        @include media(mobile) {
          @include fontMontserrat(18px, 700);
          margin: 50px 0px;
          line-height: 19px;
        }

        span {
          display: block;
          text-transform: uppercase;
          @include fontMontserrat(40px, 700);
          margin-left: 20px;

          @include media(mobile) {
            @include fontMontserrat(33px, 700);
          }
        }
      }

      .money {
        position: absolute;
        @include fontMontserrat(44px, 700);
        color: #5fac28;
        margin: 150px -271px;
        line-height: 60px;

        @include media(mobile) {
          margin: 99px -177px;
          line-height: 40px;
          @include fontMontserrat(28px, 700);
          color: #5fac28;
        }

        span {
          @include fontMontserrat(80px, 700);
          color: #5fac28;

          @include media(mobile) {
            @include fontMontserrat(60px, 700);
            color: #5fac28;
          }
        }
      }

      .money-right {
        width: 52%;

        .money-cross {
          @include media(mobile) {
            display: none;
          }
        }

        .money-mb {
          @include media(desktop) {
            display: none;
          }
        }

        @include media(mobile) {
          width: 100%;
          margin-top: -30px;
        }

        .title {
          text-transform: uppercase;
          @include fontMontserrat(22px, 700);
          color: $black;
          position: absolute;
          margin: 105px 119px;
          line-height: 26px;

          @include media(mobile) {
            @include fontMontserrat(18px, 700);
            margin: 50px 0px;
            line-height: 19px;
          }

          span {
            display: block;
            text-transform: uppercase;
            @include fontMontserrat(40px, 700);
            margin-left: 50px;

            @include media(mobile) {
              margin-left: 40px;
              @include fontMontserrat(33px, 700);
            }
          }
        }

        .prices {
          display: flex;
          justify-content: space-between;
          position: relative;
          margin: 25px -31px 3px 125px;
          

          @include media(mobile) {
            padding: 0;
            margin: 30px 0;
          }

    
          p {
            text-transform: uppercase;
            @include fontMontserrat(22px, 700);
            line-height: 35px;
            padding-bottom: 10px;

            @include media(mobile) {
              @include fontMontserrat(14px, 700);
            }

            span {
              @include fontMontserrat(44px, 700);
              display: block;
              color: #5fac28;

              @include media(mobile) {
                @include fontMontserrat(30px, 700);
                color: #5fac28;
              }
            }
          }
        }

        .source{
          margin: 25px -31px 3px 125px;
          

          @include media(mobile) {
            padding: 0;
            margin: 30px 0;
          }
        }
      }
    }
    .buy {
      margin-bottom: 80px;

      .overlay-buy {
        background: url("../img/border-buy.svg") no-repeat 40px;
        width: 1490px;
        height: 400px;
        position: absolute;
        margin: -60px -100px;

        @include media(mobile) {
          background: none;
        }
      }

      p {
        @include fontMontserrat(22px, 500);
        margin-bottom: 40px;
      }

      .buy-content {
        display: flex;
        justify-content: space-between;
        padding: 0 22px;

        @include media(mobile) {
          flex-direction: column;
        }

        .item {
          width: 100%;
          text-align: center;

          img {
            margin-bottom: 46px;
          }

          p {
            @include fontMontserrat(22px, 700);
          }
        }

        .item-coin {
          position: relative;
          bottom: 86px;
          left: 110px;

          @include media(mobile) {
            position: absolute;
            bottom: auto;
            left: auto;
            margin: -180px 179px;
            transform: scale(0.3);
          }
        }
      }
    }

    .economy {
      display: flex;
      justify-content: space-between;

      @include media(mobile) {
        flex-direction: column;
      }

      .title-ray {
        @include fontMontserrat(29px, 700);
        text-align: center;
        position: relative;
        text-transform: uppercase;
        right: 52px;
        top: 40px;

        @include media(mobile) {
          right: 0;
          top: 0;
          margin-bottom: 30px;
        }
      }

      .item-ray {
        position: relative;
        right: 150px;

        @include media(mobile) {
          right: 0;
        }
      }

      .number-economy {
        position: absolute;
        @include fontMontserrat(54px, 700);
        color: $blue-xl;
        margin: -410px 165px;
        line-height: 120px;

        @include media(mobile) {
          margin: -169px 110px;
          @include fontMontserrat(41px, 700);
          color: $blue-xl;
          line-height: 40px;

          br {
            display: none;
          }
        }

        span {
          @include fontMontserrat(130px, 700);
          color: $blue-xl;

          @include media(mobile) {
            @include fontMontserrat(60px, 700);
            color: $blue-xl;
          }
        }
      }

      .box-consume {
        padding: 10px;
        background: rgba($light-blue, 0.05);
        border-radius: 12px;
        padding: 38px 100px 38px 38px;
        height: 750px;

        @include media(mobile) {
          padding-right: 70px;
          margin-top: 40px;
        }

        .content-box {
          @include fontMontserrat(22px, 400);
          line-height: 28px;
          margin-bottom: 50px;

          @include media(mobile) {
            @include fontMontserrat(18px, 400);
            color: $gray;
          }

          strong {
            @include fontMontserrat(40px, 700);
            color: $gray;

            @include media(mobile) {
              @include fontMontserrat(22px, 700);
              color: $gray;
              br {
                display: none;
              }
            }
          }
          span {
            @include fontMontserrat(44px, 700);
            color: $gray;
            line-height: 120px;

            @include media(mobile) {
              @include fontMontserrat(40px, 700);
              color: $gray;
              line-height: 50px;
            }

            strong {
              @include fontMontserrat(80px, 700);
              color: $gray;

              @include media(mobile) {
                @include fontMontserrat(40px, 700);
                color: $gray;
                line-height: 30px;
              }
            }
          }
        }

        .content-calculator {
          @include fontMontserrat(22px, 400);
          line-height: 28px;

          @include media(mobile) {
            @include fontMontserrat(18px, 400);
            line-height: 28px;

            br {
              display: none;
            }
          }
          strong {
            @include fontMontserrat(40px, 700);
            color: $gray;

            @include media(mobile) {
              @include fontMontserrat(24px, 700);
              color: $gray;

              br {
                display: none;
              }
            }
          }
          span {
            @include fontMontserrat(44px, 700);
            color: #5fac28;
            line-height: 56px;
            position: relative;
            top: 20px;

            @include media(mobile) {
              @include fontMontserrat(40px, 700);
              color: #5fac28;
            }

            strong {
              @include fontMontserrat(80px, 700);
              color: #5fac28;

              @include media(mobile) {
                @include fontMontserrat(40px, 700);
                color: #5fac28;
              }
            }
          }
        }

        .model {
          position: absolute;
          margin: 120px 0;
          @include fontMontserrat(20px, 300);

          @include media(mobile) {
            margin: 50px 0;
            @include fontMontserrat(18px, 300);
          }
        }

        .air-wind {
          position: absolute;
          margin: 30px 410px;

          @include media(mobile) {
            margin: 30px 182px;
            transform: scale(0.8);
          }
        }

        .calculator {
          position: absolute;
          margin: -185px 270px;

          @include media(mobile) {
            margin: -111px 56px;
            transform: scale(0.45);
          }
        }
      }
    }
  }
}

.line-air {
  position: absolute;
  margin: -225px 576px;

  @include media(mobile) {
    margin: -1530px 135px;
    transform: scale(0.3);
  }
}

.snow-detail {
  position: absolute;
  margin: -80px -170px;

  @include media(mobile) {
    margin: -33px -250px;
  }
}
